                        提交演练项目
                        <span class="el-icon-close" @click="closeBullet()"></span>
                    </div>
                    <div class="bulletMain">
                        <p>恭喜，您已成功创建演练项目。</p>
                        <p>演练名称: {{ ruleForm.form1[0].drillName }}</p>
                        <p class="detail">演练详情: {{ ruleForm.form1[0].drillDetail }}</p>
                        <p>开始演练时间: {{ timeData.valueDate }} {{ timeData.valueTime }}</p>
                    </div>
