</template>

<script>
	export default {
		data() {
			var checkNum = (rule, value, callback) => {
				if(value != ''){
					if(Number(value)){
						if (value <= 0) {
							callback(new Error('输入值必须为正数'));
						} else {
							//正确
							callback();
						}
					} else {
						if(value == 0){
							callback(new Error('输入值必须为正数'));
						} else {
							callback(new Error('输入值必须为数字'));
						}
					}
				} else {
				}
			};
			return {
				newLoading: false, //loading
				titleChoose: '1',       //选择的步骤
				linkReady: {
					first: false,
					second: false,
					three: false,
					fourth: false,
					fifth: false,
				},//完成的环节
				allReady: false,    //所有的都是已经填写
				showBullet: false,
				//第一部分
				ruleForm: {
					form1: [
						{
							drillName: '',        //演练名称
							drillDetail: '',      //演练详情
						}
					]
				},
				//第二部分环节设计
				linkDesignForm: {
					lists: [
						{
							linkNum: '环节一',     //环节编号
							node_name: '',          //环节名称 *
							//场景素材 *
							fileList: [],         //保存页面上显示的素材文件
							description: '',        //文字素材
							attachment_id: [],      //保存传入后台的素材内容(素材id)
							a_time: '',             //素材倒计时
							describe: '',           //题目 *
							problem_time: '',       //答题时间 *
							answer: '',             //参考答案
							score: '',              //问题分数 *
							score_time:'',          //打分时间
							time: '',               //点评时间
							remark: '',             //点评专家显示在页面
							score_user: '',     //点评人id 传入后台的
							remarkData: [],      //  保存   选取的点评人员id
						}
					]
				},
				btnFileIndex: '',     //选取文件的按钮序号
				timeRules: { validator: checkNum, },  //动态时间验证
				answerTimeRules: [
					{required: true, message: '请输入答题时间'},
					{ validator: checkNum, },
				],  //答题时间验证
				scoreRules: [
					{required: true, message: '请输入问题分数'},
					{ validator: checkNum, },
				],    //问题分数验证
				//第三部分参演名单
				peopleForm: false,  //显示人员表
				ruleForm3: {
					director: '',         //导演组
					directorData: [],    //选取的导演人员
					group: '',            //参演小组
					groupData: [],       //选取的参演人员
					professor: '',        //评分专家
					professorData1: [],  //选取的评分人员
				},
				treeClickIndex: '',     //点击人员库的对象id
				treeSearchInput: '',    //搜索值
				treeSelectValue:'按姓名',     //条件
				treeOptions: [
					{
						value: '按姓名',
						label: '按姓名',
					},
					{
						value: '按部门',
						label: '按部门',
					},
				],
				//人员表
				peoPleData: [],           //所有人员
				disPeople: [],            //所有人员 包括不能被选的人员（已经被其他的选取）
				checkedPeople: [],       //该类型处已经被选取的人员
				//第四 设置分组
				setGroupData:{
					groupChooseNum: '',   //选择第几种分组
					//按模版分组
					radioTemplate: '1',   //模板分组值
					groupA: ["参演A组","参演B组","参演C组","参演D组","参演**组"],
					group1: ["参演1组","参演2组","参演3组","参演4组","参演**组"],
					groupNum: '',         //模板分组的组数
					haveGroupNum: false,  //是否输入了模板分组的组数
					//自定义分组
					customLists: [
						{groupValue: ''},
					],
				},
				timeData:{
					//第五部分
					chooseDate: '',       //选择演练开始的日期
					chooseTime: '',       //选择演练开始的时间
					//第六部分
					valueDate: '',        //日期
					valueTime: '',        //时间
				},
				pickerOptions1: {
					disabledDate(time) {
						return (time.getTime()+3600*24*1000) < Date.now();
					},
					shortcuts: [{
						text: '今天',
						onClick(picker) {
							picker.$emit('pick', new Date());
						}
					}, {
						text: '昨天',
						onClick(picker) {
							const date = new Date();
							date.setTime(date.getTime() - 3600 * 1000 * 24);
							picker.$emit('pick', date);
						}
					}, {
						text: '一周前',
						onClick(picker) {
							const date = new Date();
							date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
							picker.$emit('pick', date);
						}
					}]
				},
				//有编辑的状态
				editStatus: {
					first: false,
					second: false,
					three: false,
					fourth: false,
					fifth: false,
				},
				//草稿
				user_id: '',  //用户id
				draft_id: '', //草稿id
				draft_time: '',   //自动保存草稿的定时器
			}
		},
		watch: {
			treeSearchInput(val) {
				this.$refs.tree.filter(val);
			}
		},
		//防止写了之后未提交就离开
		beforeRouteLeave(to, from, next){
			//验证第一模块
			if(this.ruleForm.form1[0].drillName||this.ruleForm.form1[0].drillDetail){
				this.editStatus.first = true;
			} else {
				this.editStatus.first = false;
			}
			//第二模块
			for(let i in this.linkDesignForm.lists){
				if(
					this.linkDesignForm.lists[i].node_name||
					this.linkDesignForm.lists[i].description||
					this.linkDesignForm.lists[i].attachment_id.length != 0||
					this.linkDesignForm.lists[i].a_time||
					this.linkDesignForm.lists[i].describe||
					this.linkDesignForm.lists[i].problem_time||
					this.linkDesignForm.lists[i].answer||
					this.linkDesignForm.lists[i].score||
					this.linkDesignForm.lists[i].score_time||
					this.linkDesignForm.lists[i].time||
					this.linkDesignForm.lists[i].remark
				){
					this.editStatus.second = true;
				} else {
					this.editStatus.second = false;
				}
			}
			//第三模块
			for(let i in this.ruleForm3){
				if(this.ruleForm3[i] != ''){
					this.editStatus.three = true;
					break;
				} else {
					this.editStatus.three = false;
				}
			}
			//第四模块  分组
			if(this.setGroupData.groupChooseNum != ''){
				this.editStatus.fourth = true;
			} else {
				this.editStatus.fifth = false;
			}
			//第五模块
			if(this.timeData.valueTime != ''||this.timeData.valueDate != ''){
				this.editStatus.fifth = true;
			} else {
				this.editStatus.fifth = false;
			}
			if(this.editStatus.first||this.editStatus.second||this.editStatus.three||this.editStatus.fourth||this.editStatus.fifth){
				if(this.showBullet){
					next()
				} else {
					const answer = window.confirm('内容还未提交，是否离开？')
					if (answer) {
						next()
					} else {
						this.$store.state.showHeader = '2';
						next(false)
					}
				}
			} else {
				next();
			}
		},
		created() {
			this.$store.state.showHeader = '2';
			this.user_id = JSON.parse(localStorage['user']).id;
			this.watchDraft();
			this.getPeopleBank();//获取人员库
		},
		mounted() {
			this.draft_time = setInterval(()=>{
				this.pushDraft();
			},30000);
		},
		methods: {
			//查看是否有草稿
			watchDraft(){
				this.$axios.post('/api/is-draft',{
					user_id: this.user_id,
				}).then((response)=>{
//            		console.log(response);
					if(response.data.status == 0){
						const answer = window.confirm('您有未提交的草稿，是否继续编辑？');
						if (answer) {
							this.ruleForm = JSON.parse(response.data.data.one);
							this.linkDesignForm = JSON.parse(response.data.data.two);
							this.ruleForm3 = JSON.parse(response.data.data.three);
							this.setGroupData = JSON.parse(response.data.data.four);
							this.timeData = JSON.parse(response.data.data.five);
							this.draft_id = response.data.data.id;
							this.checkout();
						} else {
							this.draft_id = response.data.data.id;
						}
					}
				})
			},
			//存草稿
			pushDraft(){
				this.$axios.post('/api/the-draft',{
					user_id: this.user_id,
