			//获取人员库
			getPeopleBank(){
				let self = this;
				this.$axios.post('/api/people-section').then(function(response){
					//                  console.log(response.data.data);
					let region = [];  //地区
					for(let i in response.data.data){
						//获取地区 并去重
						region.push(response.data.data[i].section_one);
						let res = [];
						for (let i = 0, len = region.length; i < len; i++) {
							let current = region[i];
							if (res.indexOf(current) === -1) {
								res.push(current)
							}
						}
						region = res;
					};
					let arr = [];
					//将地区推入数组
					for(let i in region){
						arr.push({
							label: region[i],
							level: 0,
							children: [],
							id: '000000' + i,
						});
					}
					//获取二级部门
					for(let i in response.data.data){
						for(let x in arr){
							if(response.data.data[i].section_one == arr[x].label){
								arr[x].children.push({
									label: response.data.data[i].section_two,
									level: 1,
									children: [],
									id: '111111' + response.data.data[i].id,
								})
							}
						}
					}
					//获取部门人员
					for(let i in response.data.data){
						for(let x in arr){
							for(let y in arr[x].children){
								let str1 = response.data.data[i].section_one + response.data.data[i].section_two;
								let str2 = arr[x].label + arr[x].children[y].label;
								if(str1 == str2){
									arr[x].children[y].children = response.data.data[i].user;
									for(let q in arr[x].children[y].children){
										arr[x].children[y].children[q].label = arr[x].children[y].children[q].name;
										arr[x].children[y].children[q].level = 2;
									}
								}

							}
						}
					}
					//                  self.peoPleData = arr;
					self.disPeople = arr;
				}).catch(function (error) {
				})
			},
			//去重
			unique(array) {
				var obj = {};
				return array.filter(function(item, index, array){
					//console.log(typeof item + JSON.stringify(item))
					return obj.hasOwnProperty(typeof item + JSON.stringify(item)) ? false : (obj[typeof item + JSON.stringify(item)] = true)
				})
			},
			//切换
			changeClick(val){
				let self = this;
				this.checkout();
				if(val != '6'){
					//设置高亮
					this.titleChoose = val;
				} else {
					//如果步骤都完成   //提交演练，并显示成功
					if(this.allReady == true){
						this.newLoading = true;
						/**********人员名单*********/
						let charArr = [];
						charArr = this.ruleForm3.directorData;
						for(let i in this.ruleForm3.groupData){
							charArr.push(this.ruleForm3.groupData[i]);
						}
						for(let i in this.ruleForm3.professorData1){
							charArr.push(this.ruleForm3.professorData1[i])
						}
						charArr = this.unique(charArr);
						/****获取第四部分的数据****/
						let arrFourth = []; //新建一个数组保存分组情况
						if(this.setGroupData.groupChooseNum == '1'){   //不分组
							arrFourth.push('此演练不分组');
						} else if(this.setGroupData.groupChooseNum =='2'){ //第2种
							if(this.setGroupData.radioTemplate == '2'){
								for(let i=1; i <= this.setGroupData.groupNum; i++){
									let str = "参演" + i + "组";
									arrFourth.push(str);
								}
							} else if(this.setGroupData.radioTemplate == '1'){ //ABC(65~90)
								for(let i=0; i < this.setGroupData.groupNum; i++){
									let str = "参演" + String.fromCharCode(65+i) + "组";
									arrFourth.push(str)
								}
							}
						} else if(this.setGroupData.groupChooseNum == '3'){ //自定义
							//自定义
							for(let i in this.setGroupData.customLists){
								arrFourth.push(this.setGroupData.customLists[i].groupValue);
							}
						}
						/******第五部分开始时间*****/
						let startTime = this.timeData.valueDate + ' ' + this.timeData.valueTime;
						/*****调接口****/
						this.$axios.post('/api/design-add',{
							draft_id: this.draft_id,
							name: this.ruleForm.form1[0].drillName,
							description: this.ruleForm.form1[0].drillDetail,
							design_time: startTime,
							group_name: arrFourth,  //分组名称
							character2: charArr,    //参演人员
							node_name: this.linkDesignForm.lists,
						}).then(function(response){
							//                          console.log(response);
							self.newLoading = false;
							if(response.data.status == '0'){
								self.showBullet = true;
							} else {
								self.$message({
									message: '提交演练项目失败！',
									type: 'warning'
								});
							}
						}).catch(function (error) {
						})
					}
				}
			},
