			//验证是否已经填写
			checkout(){
				//验证第一步骤
				for(let i in this.ruleForm.form1){
					for(let j in this.ruleForm.form1[i]){
						if(this.ruleForm.form1[i][j] == ''){
							this.linkReady.first = false;
							break;
						} else {
							this.linkReady.first = true;
						}
					}
				}
				//第二模块
				for(let i in this.linkDesignForm.lists){
					//遍历每项
					let strLength = this.linkDesignForm.lists[i].node_name.length;
					if(
						strLength>=3 && strLength<=7&&            //环节名称 3 < x < 7
						this.linkDesignForm.lists[i].describe&&       //题目
						this.linkDesignForm.lists[i].score>0&&        //问题分数 答题时间必填大于0
						this.linkDesignForm.lists[i].problem_time>0&&
						//素材验证
						(this.linkDesignForm.lists[i].attachment_id != ''||this.linkDesignForm.lists[i].description!= '')
					){//如果以上必填都满足，判断非必填的时间是否满足条件
						//没有素材倒计时或者有素材倒计时且大于0
						if(this.linkDesignForm.lists[i].a_time == ''||this.linkDesignForm.lists[i].a_time>0){
							this.linkReady.second = true;
							//没有点评时间或者有点评时间而且大于0
							if(this.linkDesignForm.lists[i].time == ''||this.linkDesignForm.lists[i].time>0){
								this.linkReady.second = true;
								//没有打分时间或者 打分时间大于0
								if(this.linkDesignForm.lists[i].score_time == ''||this.linkDesignForm.lists[i].score_time>0 ){
									this.linkReady.second = true;
								} else {
									this.linkReady.second = false;
								}
							} else {
								//有点评时间但是小于等于0
								this.linkReady.second = false;
							}
						} else {
							//有素材倒计时但小于等于0
							this.linkReady.second = false;
						}

					} else {
						this.linkReady.second = false;
						break;
					}
				}
				//第三模块
				for(let i in this.ruleForm3){
					if(this.ruleForm3[i] == ''){
						this.linkReady.three = false;
						break;
					} else {
						this.linkReady.three = true;
					}
				}
				//第四模块
				if(this.setGroupData.groupChooseNum == '1'){
					this.linkReady.fourth = true;
				} else if(this.setGroupData.groupChooseNum == '2'){
					if(this.setGroupData.groupNum !=''){
						this.linkReady.fourth = true;
					} else {
						this.linkReady.fourth = false;
					}
				} else if(this.setGroupData.groupChooseNum == '3'){
					for(let i in this.setGroupData.customLists){
						if(this.setGroupData.customLists[i].groupValue != ''){
							this.linkReady.fourth = true;
						} else {
							this.linkReady.fourth = false;
							break;
						}
					}
				} else {
					this.linkReady.fourth = false;
				}
				//第五模块
				if(this.timeData.valueDate && this.timeData.valueTime){
					this.linkReady.fifth = true;
				} else {
					this.linkReady.fifth = false;
				}
				// 判断所有的是否都已经填写，如果是就可以点提交按钮
				if(
					this.linkReady.first&&
					this.linkReady.second&&
					this.linkReady.three&&
					this.linkReady.fourth&&
					this.linkReady.fifth
				){
					this.allReady = true;
				} else {
					this.allReady = false;
				}
			},
