							//遍历2级每一项的children 拼接数据
							for(let y in allData[x].children){
								//三级  人员名称
								for(let z in allData[x].children[y].children){
//                                  str += (allData[x].label + ' ' + allData[x].children[y].label +  ' ' + allData[x].children[y].children[z].label) + ',';
									arr0.push(allData[x].label + ' ' + allData[x].children[y].label +  ' ' + allData[x].children[y].children[z].label)
									arr1.push({
										user_id:  allData[x].children[y].children[z].id,
										character:  '',
									});
								}
							};
						} else {
							//否则代表选取了第二级或者第三级的  //遍历第二级
							for(let y in allData[x].children){
								//如果第二级的和选中的某一项相等
								if(allData[x].children[y] == chooseItem[i]){
									for(let z in allData[x].children[y].children){
//                                      str += (allData[x].label + ' ' + allData[x].children[y].label +  ' ' + allData[x].children[y].children[z].label) + ',';
										arr0.push(allData[x].label + ' ' + allData[x].children[y].label +  ' ' + allData[x].children[y].children[z].label)
										arr1.push({
											user_id:  allData[x].children[y].children[z].id,
											character:  '',
										});
									}
								} else {
									//第三级相等
									for(let z in allData[x].children[y].children){
										if(allData[x].children[y].children[z] == chooseItem[i]){
//                                          str += (allData[x].label + ' ' + allData[x].children[y].label +  ' ' + allData[x].children[y].children[z].label) + ',';
											arr0.push(allData[x].label + ' ' + allData[x].children[y].label +  ' ' + allData[x].children[y].children[z].label)
											arr1.push({
												user_id:  allData[x].children[y].children[z].id,
												character:  '',
											});
										}
									}
								}
							}
						}
					}
				}
				arr0 = this.unique(arr0);
				arr1 = this.unique(arr1);
				for(let i in arr0){
					str += (arr0[i] + ',');
				}
				if(this.treeClickIndex == 'director'){
					//导演组
					this.ruleForm3.director = str.substr(0, str.length - 1);
					for(let i in arr1){
						arr1[i].character = '0';
					}
					this.ruleForm3.directorData = arr1;    //选取的导演人员(应该只有一人)
				} else if(this.treeClickIndex == 'group'){
					//参演组
					this.ruleForm3.group = str.substr(0, str.length - 1);
					for(let i in arr1){
						arr1[i].character = '1'
					}
					this.ruleForm3.groupData = arr1;      //选取的参演人员
				} else if(this.treeClickIndex == 'professor'){
					//评分组
					this.ruleForm3.professor = str.substr(0, str.length - 1);
					for(let i in arr1){
						arr1[i].character = '2';
					}
					this.ruleForm3.professorData1 = arr1;  //选取的评分人员
				} else {
					//点评1
					this.linkDesignForm.lists[this.treeClickIndex].remark = str.substr(0, str.length - 1);
					let test = [];
					if(arr1.length == 0){//没选
						this.linkDesignForm.lists[this.treeClickIndex].score_user = '';
						this.linkDesignForm.lists[this.treeClickIndex].remarkData = [];
					} else {
						test.push({
							user_id: arr1[0].user_id
						});
						this.linkDesignForm.lists[this.treeClickIndex].score_user = arr1[0].user_id;  //保存当前已选的
						this.linkDesignForm.lists[this.treeClickIndex].remarkData.push(arr1[0].user_id);  //保存当前已选的
					}
				}
				//关闭模态框
				this.peopleForm = false;
				this.checkout();
			},
			//点击的树状图的节点
			handleNodeClick(data) {
//              console.log(data);
			},
			//节点选中状态发生变化时的回调
			handleCheckChange(data, checked, indeterminate) {
				//传递给 data 属性的数组中该节点所对应的对象、节点本身是否被选中、节点的子树中是否有被选中的节点
//              console.log(data, checked, indeterminate);
				if(this.treeClickIndex == 'group'||this.treeClickIndex == 'professor'){
				} else {
					if(checked){
						if(data.level == 2){
							let arr = [data];
							this.$refs.tree.setCheckedNodes(arr);
						}
					}
				}
			},
			/***444444444444444444444444444444*/
			//选择分组
			chooseGroupTitle(val){
				this.setGroupData.groupChooseNum = val;
				if(val == 2&&this.setGroupData.groupNum == ''){
					this.setGroupData.haveGroupNum = true;
				} else {
					this.setGroupData.haveGroupNum = false;
				}
				this.checkout();
			},
			//选择单选（第二种）
			chooseSecondGroup(){
				if(this.setGroupData.groupNum != ''){
					this.setGroupData.groupChooseNum = 2;
					this.setGroupData.haveGroupNum = false;
				} else {
					this.setGroupData.groupChooseNum = '';
					this.setGroupData.haveGroupNum = true;
				}
				this.checkout();
			},
			//自定义
			chooseThirdGroup(){
				this.setGroupData.groupChooseNum = 3;
			},
			// 新增自定义分组
			addGroup(){
				this.setGroupData.customLists.push(
					{
						groupValue: ''
					},
				)
			},
			//删除自定义分组
			deleteGroup(val){
				this.setGroupData.customLists.splice(val, 1);
//			  console.log(this.setGroupData.customLists)
			},
			/**************第555555555555555555步*************/
			//选择日期
			onPickDate(maxDate){
//              console.log(maxDate);
				if(maxDate == null){
					this.timeData.valueDate = '';
				} else {
					let date = new Date(maxDate);
					let Y = date.getFullYear() + '-';
					let M = (date.getMonth()+1 < 10 ? '0'+(date.getMonth()+1) : date.getMonth()+1) + '-';
					//let D = date.getDate();
					let D = date.getDate() < 10 ? '0'+date.getDate() : date.getDate();
					//let h = date.getHours() + ':';
					//let m = date.getMinutes() + ':';
					//let s = date.getSeconds();
					//console.log(Y+M+D);   //日期
					this.timeData.valueDate = Y+M+D;
				}
				this.checkout();
			},
			onPickTime(val){
				if( val == null ){
					this.timeData.valueTime = '';
				} else {
					let date = new Date(val);
					let h = (date.getHours() < 10 ? '0'+date.getHours() : date.getHours()) + ':';
					let m = (date.getMinutes() < 10 ? '0'+date.getMinutes() : date.getMinutes()) + ':';
					let s = date.getSeconds() < 10 ? '0'+date.getSeconds() : date.getSeconds();
					//console.log(h + m +s);
					this.timeData.valueTime = h + m + s;
				}
				this.checkout();
			},
			onFocusTime(){
				this.timeData.valueTime = '';
				this.timeData.chooseTime = '';
				console.log(this.timeData)
			},
			/************6666666666666*************/
			//关闭弹框
			closeBullet(){
				this.showBullet = false;
				window.location.reload();
			},
			ableToSee(){
				this.$router.push('/enterable');
			}
		},
	}
</script>

